<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='less'>


@import "../assets/base.less";  

header {
  padding: 0 12% 30px 12%;
  background: @bgColor;
  display: grid;
  justify-items: center;

  h1 {
    color: #fff;
